<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>

		<b-form>

			<b-form-group label="*Fecha" label-for="fecharealizacion">
				<b-form-input id="fecharealizacion"
							  v-model="ventas.fecharealizacion"
							  type="date"
							  :disabled="repartohabilitado"
							  placeholder="Ingrese la fecha de la venta"
							  invalid-feedback="Complete este campo"
							  :state="validacion.fecharealizacion.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="fecharealizacion-live-feedback">
					{{ validacion.fecharealizacion.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Tipo de Venta"
						  label-for="tipo_venta"
						  @submit.stop.prevent="handleSubmit">
				<b-form-select id="tipo_venta"
							   v-model="ventas.tipo_venta"
							   type="text"
							   @change="cambiodetipo()"
							   placeholder="Describa el tipo de venta"
							   invalid-feedback="Complete este campo"
							   :state="validacion.tipo_venta.estado"
							   required
							   :options="tipos_productos">
				</b-form-select>
				<b-form-invalid-feedback id="tipo_venta-live-feedback">
					{{ validacion.tipo_venta.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Cliente"
						  label-for="id_cliente"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_cliente"
								:state="validacion.id_cliente.estado"
								v-model="ventas.id_cliente"
								type="text"
								placeholder="Ingrese un Cliente"
								invalid-feedback="Complete este campo"
								required
								solo
								filled
								@change="setCliente()"
								:items="op_clientes">
				</v-autocomplete>
				<b-form-invalid-feedback id="id_cliente-live-feedback">
					{{ validacion.id_cliente.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Reparto"
						  label-for="id_reparto"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_reparto"
								ref="id_repartoselect"
								:state="validacion.id_reparto.estado"
								v-model="ventas.id_reparto"
								type="text"
								placeholder="Ingrese un Reparto"
								invalid-feedback="Complete este campo"
								required
								:disabled="repartohabilitado"
								solo
								filled
								:items="op_repartos">
				</v-autocomplete>
				<b-form-invalid-feedback id="id_reparto-live-feedback">
					{{ validacion.id_reparto.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Stock"
						  label-for="id_stock"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_stock"
								:state="validacion.id_stock.estado"
								v-model="ventas.id_stock"
								type="text"
								placeholder="Ingrese un Stock"
								invalid-feedback="Complete este campo"
								@change="setStock()"
								required
								:disabled="stockhabilitado"
								solo
								filled
								:items="op_stocks">
				</v-autocomplete>
				<b-form-invalid-feedback id="id_stock-live-feedback">
					{{ validacion.id_stock.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<!----------    Beneficios         -------------->
			<!--<b-button class="my-3"
	variant="primary"
	@click="sumarProductos()"
	style="color: white">¿Agregar producto?</b-button>-->
			<div v-show="btn_producto" v-for="(item, index) in ventas.componentes" :key="index">
				<b-form>
					<div class="d-flex  justify-content-between">

						<h4>Producto: {{ index + 1 }}</h4>
						<b-button variant="danger"
								  @click="eliminarProducto(index)"
								  style="color: white">
							<v-icon style="color: white"> mdi-delete </v-icon>
						</b-button>
					</div>
					<b-form-group label="*Producto"
								  label-for="producto"
								  @submit.stop.prevent="handleSubmit">
						<v-autocomplete id="producto"
										:state="validacion.componentes[index].producto.estado"
										v-model="item.producto"
										type="text"
										placeholder="Ingrese un Numero"
										invalid-feedback="Complete este campo"
										required
										solo
										@change="cambiarUnitario(index)"
										filled
										:items="op_productos">
						</v-autocomplete>
						<b-form-invalid-feedback id="producto-live-feedback">
							{{ validacion.componentes[index].producto.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<b-form-group label="*Cantidad" label-for="cantidad">
						<b-form-input id="cantidad"
									  :state="validacion.componentes[index].cantidad.estado"
									  v-model="item.cantidad"
									  type="number"
									  placeholder="*Ingrese la cantidad"
									  invalid-feedback="Complete este campo"
									  required
									  v-on:keyup="sumar(item.cantidad, item.precio);"
									  @change="checkearstock(item.producto, item.cantidad)">
						</b-form-input>
						<b-form-invalid-feedback id="cantidad-live-feedback">
							{{ validacion.componentes[index].cantidad.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<b-form-group label="*Precio Unitario" label-for="precio">
						<span style="display:inline-block;width:3%;">$</span>
						<b-form-input id="precio"
									  :state="validacion.componentes[index].precio.estado"
									  v-model="item.precio"
									  type="number"
									  style="display:inline-block;width:97%;"
									  placeholder="*Ingrese el precio"
									  invalid-feedback="Complete este campo"
									  required
									  v-on:keyup="sumar(item.cantidad, item.precio)">
						</b-form-input>
						<b-form-invalid-feedback id="precio-live-feedback">
							{{ validacion.componentes[index].precio.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<span>Subtotal: $</span>{{item.cantidad * item.precio}}
				</b-form>
				<b-button class="my-3"
						  variant="success"
						  @click="sumarProductos()"
						  style="color: white">+</b-button>
			</div>

			<br><br><span style="text-align:center;width:100%;">Saldo del cliente: ${{saldototal}}</span>
			<br><span style="text-align:center;width:100%;">Total de la venta: ${{totalpago}}</span>
			<br><span style="text-align:center;width:100%;">El cliente deberia pagar: ${{totalpago + saldototal}}</span><br><br>

			<b-form-group label="*Total Pagado en Efectivo" label-for="totalpagado">
				<b-form-input id="totalpagado"
							  v-model="ventas.totalpagado"
							  type="number"
							  placeholder="Ingrese el Total que pago el cliente"
							  invalid-feedback="Complete este campo"
							  :state="validacion.totalpagado.estado"
							  required
							  v-on:keyup="calcularsaldazo()">
				</b-form-input>
				<b-form-invalid-feedback id="totalpagado-live-feedback">
					{{ validacion.totalpagado.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Total Pagado en Mercado Pago" label-for="totalpagado">
				<b-form-input id="totalpagadomp"
							  v-model="ventas.totalpagadomp"
							  type="number"
							  placeholder="Ingrese el Total que pago el cliente"
							  invalid-feedback="Complete este campo"
							  :state="validacion.totalpagadomp.estado"
							  required
							  v-on:keyup="calcularsaldazo()">
				</b-form-input>
				<b-form-invalid-feedback id="totalpagadomp-live-feedback">
					{{ validacion.totalpagadomp.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>

		<br><span style="text-align:center;width:100%;">El nuevo saldo seria: ${{saldazo}}</span><br><br>

		<b-button class="mt-2" variant="success" block @click="checkearsaldos()">Guardar</b-button>
		<b-modal id="modal_excedido"
				 ref="my-modal2"
				 hide-footer
				 title="Confirmar Excedido"
				 ok-only>
			<h5>
				El nuevo saldo supera el permitido. Solicitar autorización.
			</h5>
			<p>
				Saldo Permitido: ${{limite}}</p>
			<p>	Saldo Nuevo: ${{saldazo}}</p>
			<p>	Excedente: ${{saldazo - limite}}
			</p>
			<h5>¿Desea continuar de todos modos?</h5>
			
			<b-button class="mt-2" block @click="hideModal2" title="Volver Atras">
				No
			</b-button>

			<b-button class="mt-3"
					  variant="success"
					  block
					  title="Confirmar"
					  @click="showModal()">
				Confirmar
			</b-button>
		</b-modal>
		<b-modal id="modal_confirmar"
				 ref="my-modal"
				 hide-footer
				 title="Confirmar Venta"
				 ok-only>
			<div class="d-block text-center" v-if="this.infoConfirmar.id_cliente != null">
				<b-list-group horizontal>
					<b-list-group style="text-align:center;width:100%">
						<b-list-group-item>
							<b>Fecha de la venta:</b>
							{{ infoConfirmar.fecharealizacion }}
						</b-list-group-item>
						<b-list-group-item v-if="infoConfirmar.id_reparto != null">
							<b>Reparto:</b>
							{{ this.infoConfirmar.id_reparto }}
						</b-list-group-item>
						<b-list-group-item v-if="infoConfirmar.id_stock != null">
							<b>Stock:</b>
							{{ infoConfirmar.id_stock }}
						</b-list-group-item>
						<b-list-group-item v-if="infoConfirmar.id_cliente != null">
							<b>Cliente:</b>
							{{ getCliente(infoConfirmar.id_cliente).nombre }}
						</b-list-group-item>
						<b-list-group-item>
							<b>Venta Total:</b>
							${{ infoConfirmar.totalapagar }}
						</b-list-group-item>
						<b-list-group-item>
							<b>Cliente Paga:</b>
							${{ infoConfirmar.totalpagado }} + ${{ infoConfirmar.totalpagadomp }} = ${{ parseInt(infoConfirmar.totalpagado) + parseInt(infoConfirmar.totalpagadomp)}}
						</b-list-group-item>
						<b-list-group-item>
							<b>Saldo Nuevo:</b>
							${{ infoConfirmar.saldonuevo }}
						</b-list-group-item>
						<b-card title="Productos: " v-if="infoConfirmar.componentes.length>0">

							<b-list-inline>
								<div v-for="componente in infoConfirmar.componentes"
									 :key="componente.id" style="text-align:center;width:49%;display:inline-block;">

									<b-list-inline class="productoslista" style="width:100%;display:inline-block;">
										<b-list-group-item>
											<b>Producto:</b>
											{{
listaproduct.find(s=>{
                                                            return s.id_producto == componente.producto.split('/')[4]
                                                        }).nombre
											}}
										</b-list-group-item>
										<b-list-group-item>
											<p>

												<b>Cantidad: </b><br />
												{{componente.cantidad}}
											</p>
										</b-list-group-item>
										<b-list-group-item>
											<p>

												<b>Precio Un: </b><br />
												${{componente.precio}}
											</p>
										</b-list-group-item>
									</b-list-inline>

								</div>
							</b-list-inline>
						</b-card>
						<b-list-group-item v-else>
							Sin Productos
						</b-list-group-item>

					</b-list-group>
					&nbsp;
				</b-list-group>
			</div>
			<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
				Volver Atras
			</b-button>

			<b-button class="mt-3"
					  variant="success"
					  block
					  title="Confirmar"
					  @click="postventa()">
				Confirmar
			</b-button>
		</b-modal>
	</div>
</template>

	<script>
		import { APIControler } from "@/store/APIControler";
		import axios from "axios";
        import Vue from 'vue';
        import VueSession from 'vue-session'
        Vue.use(VueSession)

		export default {
			props: {
				updateTable: Function,
			},
			data() {
				return {
					ventas: {componentes:[]},
					data: {},
					limite:0,
					list_productos: {},
                    clienteelegido: {},
					text: "",
					repartohabilitado: false,
                    stockhabilitado: false,
					saldototal: 0,
					saldazo: 0,
					infoConfirmar: {
						fecharealizacion: null,
						id_stock: null,
						id_reparto: null,
						id_cliente: null,
						totalpagado: 0,
                        totalpagadomp: 0,
						totalapagar: 0,
						gananciatotal: 0,
						componentes: [],
					},
					stockusado: {},
					productoelegido: {},
                    tipos_productos: [
                        { value: null, text: "Elija un tipo de producto", disabled: true },
                        { value: "Pollo", text: "Pollo" },
                        { value: "Congelados", text: "Congelado" }
                    ],
					totalprod: [],
					totalpago: 0,
					stockusado: {},
					stockusado2: {},
					numero:-1,
					op_productos: [],
					listastocks: [],
                    productonuevo: {},
					listaproduct: [],
                    listacat: [],
					listacli: [],
					op_stocks: [{value:'general', text:'Stock General'}],
					op_repartos: [],
					op_clientes: [],
					validacion: {
						fecharealizacion: { estado: null, mensaje: "" },
						id_stock: { estado: null, mensaje: "" },
						id_reparto: { estado: null, mensaje: "" },
						id_cliente: { estado: null, mensaje: "" },
                        tipo_venta: { estado: null, mensaje: "" },
						totalpagado: { estado: null, mensaje: "" },
                        totalpagadomp: { estado: null, mensaje: "" },
						componentes: [],
					},
				};
			},

			methods: {

				async postventa() {
					let ventasAPI = new APIControler();
					ventasAPI.apiUrl.pathname = "ventas/";
					console.log(ventas);
					if (this.ventas.id_stock == 'general') {
						this.ventas.componentes.forEach((prod) => {
							this.actualizarStockProducto1(prod.producto.split('/')[4], parseInt(prod.cantidad))
						});
						this.ventas.id_stock = this.op_stocks[1].value;
					} else
					{
                        this.actualizarStockProducto();
					}
					this.ventas.componentes.forEach((comp) => {
						comp.costo = parseInt(this.getFechaPrecio(parseInt(comp.producto.split('/')[4])).split(':')[1])
						comp.precioref = this.getPrecioRef(comp.producto.split('/')[4]);
					});
					let respuesta = await ventasAPI.postData(this.ventas);
					this.cargarFeedback(respuesta);
					this.updateTable();
					this.actualizarSaldoCliente();
				},
				calcularsaldazo()
				{
                    let totalapagar = this.saldototal + this.totalpago;
                    this.saldazo = totalapagar - (parseInt(this.ventas.totalpagado) + parseInt(this.ventas.totalpagadomp));
				},
				checkearstock(producto, cantidad)
				{
					this.setStock();
					if (this.stockusado != null)
					{
						this.stockusado.componentes.forEach((s) => {
							if (s.producto == producto)
							{
								if (s.cantidad < cantidad)
								{
									alert('No hay suficiente stock de este producto en su camion')
								}
                                throw 'exit';
							}
						});
                        let prod = this.getProducto(producto.split('/')[4]);
                        if (prod.stocktotal < cantidad) {
                            alert('El stock del producto no alcanza');
                        }

					}
				},
				checkearsaldos()
				{
                    let totalapagar = this.saldototal + this.totalpago;
                    let saldonuevo = totalapagar - (parseInt(this.ventas.totalpagado) + parseInt(this.ventas.totalpagadomp));
					let clientefinal = this.getCliente(parseInt(this.ventas.id_cliente.split('/')[4]));
					console.log(clientefinal);
					console.log()
					this.limite = this.getLimite(clientefinal.categoria.split('/')[4]).limite;
                    if (saldonuevo > this.limite) {
                        this.$refs["my-modal2"].show();
					} else
					{
						this.showModal();
					}
				},
                getLimite(idcat) {
                    let categ = this.listacat.find(s => {
                        return s.id_categoria == idcat
                    })
                    return categ;
				},
                async getCategorias() {
                    let productosAPI = new APIControler();
                    productosAPI.apiUrl.pathname = "categorias/";
                    this.listacat = await productosAPI.getData();
                },
				showModal() {
					this.infoConfirmar.id_reparto = this.ventas.id_reparto.split('/')[4];
                    this.infoConfirmar.id_stock = this.ventas.id_stock.split('/')[4];
					this.infoConfirmar.id_cliente = this.ventas.id_cliente.split('/')[4];
					this.infoConfirmar.componentes = this.ventas.componentes;
					this.infoConfirmar.fecharealizacion = this.ventas.fecharealizacion;
                    this.infoConfirmar.totalapagar = this.saldototal + this.totalpago;
					this.infoConfirmar.totalpagado = this.ventas.totalpagado;
                    this.infoConfirmar.totalpagadomp = this.ventas.totalpagadomp;
                    this.infoConfirmar.saldonuevo = this.infoConfirmar.totalapagar - (parseInt(this.ventas.totalpagado) + parseInt(this.ventas.totalpagadomp));
                    this.$refs["my-modal"].show();
                },
                //Funcion para esconder el modal
                hideModal() {
                    this.$refs["my-modal"].hide();
				},
                hideModal2() {
                    this.$refs["my-modal2"].hide();
                },
                async actualizarStockProducto() {
                    let stockfinal = this.getStock(this.ventas.id_stock.split('/')[4])
                    console.log("Hola");
                    console.log(stockfinal);
                    this.stockusado2.nombre = stockfinal.nombre;
                    this.stockusado2.id_reparto = stockfinal.id_reparto;
                    this.stockusado2.fechastock = stockfinal.fechastock;
                    this.ventas.componentes.forEach((produc) => {
                        let id = produc.producto.split('/')[4];
                        if (this.checkprodstock(id) == null) {
                            this.productonuevo.producto = produc.producto;
							this.productonuevo.cantidad =0 - parseInt(produc.cantidad);
                            stockfinal.componentes.push(this.productonuevo);
                        } else {
                            stockfinal.componentes.forEach((compon) => {
                                if (compon.producto.split('/')[4] == id) {
                                    compon.cantidad = parseInt(compon.cantidad) - parseInt(produc.cantidad);
                                }
                            });
                        }
                        console.log("Id del producto en venta:", id);
					});
                    this.stockusado2.componentes = stockfinal.componentes;
                    console.log(JSON.parse(JSON.stringify(this.stockusado2)));
                    await axios
                        .put(
                            "http://93.188.162.174:8081/stocks/" +
                            stockfinal.id_stock +
                            "/",
                            this.stockusado
                        );
                },
                async actualizarSaldoCliente()
				{
					let clientefinal = this.getCliente(this.ventas.id_cliente.split('/')[4])
					console.log('falopa')
                    console.log(this.getCliente(this.ventas.id_cliente.split('/')[4]))
                    this.clienteelegido.numero_cliente = clientefinal.numero_cliente
                    this.clienteelegido.nombre = clientefinal.nombre
                    this.clienteelegido.cuitocuil = clientefinal.cuitocuil
                    this.clienteelegido.calle = clientefinal.calle
                    this.clienteelegido.localidad = clientefinal.localidad
                    this.clienteelegido.provincia = clientefinal.provincia
                    this.clienteelegido.cod_postal = clientefinal.cod_postal
                    this.clienteelegido.email = clientefinal.email
                    this.clienteelegido.tel_fijo = clientefinal.tel_fijo
					this.clienteelegido.tel_celular = clientefinal.tel_celular
					this.clienteelegido.categoria = clientefinal.categoria
					if (this.ventas.tipo_venta == 'Pollo') {
						this.clienteelegido.saldopollo = this.infoConfirmar.saldonuevo
						this.clienteelegido.saldoantpollo = this.saldototal
					} else
					{
						this.clienteelegido.saldocongelados = this.infoConfirmar.saldonuevo
                        this.clienteelegido.saldoantcongelados = this.saldototal
					}
					

					console.log(this.clienteelegido);
                    
                    await axios
                        .put(
                            "http://93.188.162.174:8081/clientes/" +
                            clientefinal.numero_cliente +
                            "/",
                            this.clienteelegido
                        );
				},
				getStock(id) {
					id = parseInt(id);
					console.log(id);
                    console.log(this.listastocks);
                    let stock = this.listastocks.find(s => {
                        return s.id_stock == id
                    })
                    return stock;
				},
				checkprodstock(id_producto)
				{
					let cantidad = null;
					if (this.stockusado != {})
					{
                        for (var i = 0; i < this.stockusado.componentes.length; i++) {
                            if (this.stockusado.componentes[i].producto.split('/')[4] == id_producto) {
								cantidad = this.stockusado.componentes[i].cantidad;
                            }
						}
					}
					return cantidad;
				},
                getProducto(id) {
                    let producto = this.listaproduct.find(s => {
                        return s.id_producto == id
                    })
                    return producto;
				},
				getCliente(id) {
					id = parseInt(id);
                    let cliente = this.listacli.find(s => {
                        return s.numero_cliente == id
                    })
                    return cliente;
				},
				setCliente()
				{
                    if (this.ventas.tipo_venta == 'Pollo') {
                        this.saldototal = this.getCliente(this.ventas.id_cliente.split('/')[4]).saldopollo;
                    } else {
                        this.saldototal = this.getCliente(this.ventas.id_cliente.split('/')[4]).saldocongelados;
                    }
				},
				cambiodetipo()
				{
					console.log('hola');
					this.listacli = [];
					this.op_clientes = [];
                    this.getClientes();
					this.getProductos();
				},
				cambiarUnitario(index)
				{
					this.ventas.componentes[index].precio = this.getProducto(this.ventas.componentes[index].producto.split('/')[4]).precioref
                    this.calctotalpago();
				},
				calctotalpago()
				{
					let total = 0;
					for (var i = 0; i < this.ventas.componentes.length;i++)
					{
                        if (!isNaN((this.ventas.componentes)[i].precio) && !isNaN((this.ventas.componentes)[i].cantidad)) {
                            total = total + ((this.ventas.componentes)[i].precio * (this.ventas.componentes)[i].cantidad);
						}
					}
					console.log(total);
					this.totalpago = total;
				},
				sumar(valor1, valor2) {
					var total = 0;
                    valor1 = parseInt(valor1);
                    valor2 = parseInt(valor2);// Convertir el valor a un entero (número).
                    // Aquí valido si hay un valor previo, si no hay datos, le pongo un cero "0".
                    //total = (total == null || total == undefined || total == "") ? 0 : total;
                    /* Esta es la suma. */
                    total = (parseInt(valor1) * parseInt(valor2));
                    // Colocar el resultado de la suma en el control "span".
                    (this.totalprod)[this.numero] = total;
                    this.totalprod.push(0);
                    this.totalprod.pop();
					this.calctotalpago();
				},
				async resetForm() {
					this.ventas.nombre = "";
					this.ventas.componentes = null;
				},
                async actualizarStockProducto1(id, stock) {
                    console.log(this.getProducto(id));
                    let productofinal = this.getProducto(id);
                    this.productoelegido.id_producto = productofinal.id_producto
                    this.productoelegido.nombre = productofinal.nombre
                    this.productoelegido.tipo_producto = productofinal.tipo_producto
                    this.productoelegido.letra = productofinal.letra
                    this.productoelegido.numero_producto = productofinal.numero_producto
                    this.productoelegido.codigo_unico = productofinal.codigo_unico
                    this.productoelegido.stocktotal = parseInt(productofinal.stocktotal) - parseInt(stock)
                    this.productoelegido.precioref = productofinal.precioref
                    console.log(this.productoelegido)
                    await axios
                        .put(
                            "http://93.188.162.174:8081/productos/" +
                            this.productoelegido.id_producto +
                            "/",
                            this.productoelegido
                        );
                },

                getProducto(id) {
                    if (this.listaproduct == []) {
                        return getProductos().then(() => {
                            return this.getProducto(id)
                        })
                    }
                    else {
                        return this.listaproduct.find(s => {
                            return s.id_producto == id
                        })
                    }
                },
				//Obtengo los productos
				async getProductos() {
                    let compraAPI = new APIControler();
                    compraAPI.apiUrl.pathname = "compras/";
                    this.listacompra = await compraAPI.getData();
					let productosAPI = new APIControler();
					productosAPI.apiUrl.pathname = "productos/";
					this.listaproduct = await productosAPI.getData();
					this.op_productos = [];
                    
					let option

					this.listaproduct.forEach((element) => {
						option = {};
						option.value =
							"http://93.188.162.174:8081/productos/" + element.id_producto + "/";
						option.text = element.nombre + ' - Precio Ref: $' + element.precioref;
						if (this.ventas.id_stock === "general") {
							option.text = option.text + '- Stock: ' + element.stocktotal
							if (element.tipo_producto == this.ventas.tipo_venta) {
                                this.op_productos.push(option);
							}
						} else
						{
							if (element.tipo_producto == this.ventas.tipo_venta) {
                                if (this.checkprodstock(element.id_producto) == null) {
									option.text = option.text + ' - no esta en stock';
									if (this.$session.get('tipocuenta') == 'administrador')
									{
										option.text = option.text + '-' + this.getFechaPrecio(element.id_producto);
									}
                                    this.op_productos.push(option);
                                } else {
									option.text = option.text + ' - Stock: ' + this.checkprodstock(element.id_producto);
                                    if (this.$session.get('tipocuenta') == 'administrador') {
                                        option.text = option.text + '-' + this.getFechaPrecio(element.id_producto);
                                    }
                                    this.op_productos.unshift(option);
                                }
                                console.log(option);
                            }
						}
					});
				},
                getFechaPrecio(id) {
                    let fecha = "Nunca se compro";
                    let fechaelemento = new Date(2000, 1, 1);
                    let ultimoprecio = 0;
                    console.log(this.listacompra);
                    this.listacompra.forEach((element) => {
                        fechaelemento = new Date(element.fecharealizacion)
                        element.componentes.forEach((compon) => {
                            if (compon.producto.split('/')[4] == id) {
                                ultimoprecio = compon.precio;
                                fecha = (fechaelemento.getUTCDate()) + "/" + (fechaelemento.getMonth() + 1) + "/" + (fechaelemento.getUTCFullYear());
                            }
                        });
                    });
                    return ('Ult. Cost.:' + ultimoprecio + ' - ' + fecha);
				},
				getPrecioRef(id)
				{
                    let producto = this.listaproduct.find(s => {
                        return s.id_producto == id
                    })
                    return producto.precioref;
				},
				setStock()
				{
                    console.log(this.ventas.id_stock)
					if (this.ventas.id_stock != "general") {
						this.stockusado = this.getStock(this.ventas.id_stock.split('/')[4]);
						console.log(this.stockusado)
					}
                    this.getProductos();
				},
				async getRepartos() {
					let productosAPI = new APIControler();
					productosAPI.apiUrl.pathname = "repartos/";
					this.data = await productosAPI.getData();
					let option
					this.data.forEach((element) => {
						option = {};
						option.value =
							"http://93.188.162.174:8081/repartos/" + element.id_reparto + "/";
						option.text = element.nombre;
						console.log(option);
						this.op_repartos.push(option);
					});
				},
				async getClientes() {
					let productosAPI = new APIControler();
					productosAPI.apiUrl.pathname = "clientes/";
					this.listacli = await productosAPI.getData();
					let option
                    this.listacli.forEach((element) => {
						option = {};
						option.value =
							"http://93.188.162.174:8081/clientes/" + element.numero_cliente + "/";
                        if (this.ventas.tipo_venta == 'Pollo') {
                            option.text = element.nombre + ' - Saldo: $' + element.saldopollo;
                        } else {
                            option.text = element.nombre + ' - Saldo: $' + element.saldocongelados;
                        }
						console.log(option);
						this.op_clientes.push(option);
					});
				},
				getClientesRep()
				{
					this.op_clientes = [];
                    let option
					this.listacli.forEach((element) => {
                        option = {};
                        option.value =
							"http://93.188.162.174:8081/clientes/" + element.numero_cliente + "/";
						if (this.ventas.tipo_venta == 'Pollo') {
							option.text = element.nombre + ' - Saldo: $' + element.saldopollo;
						} else
						{
                            option.text = element.nombre + ' - Saldo: $' + element.saldocongelados;
						}
						if (this.$session.get('repartused').clientes_reparto.includes(option.value))
						{
                            this.op_clientes.push(option);
						}
					});
                    console.log('god');
                    console.log(this.op_clientes);
				},
				async getStocks() {
					let productosAPI = new APIControler();
					productosAPI.apiUrl.pathname = "stocks/";
                    this.listastocks = await productosAPI.getData();
					let option
                    this.listastocks.forEach((element) => {
						option = {};
						option.value =
							"http://93.188.162.174:8081/stocks/" + element.id_stock + "/";
						option.text = 'Stock de Reparto: ' + element.id_reparto.split('/')[4] + '- Fecha: ' + element.fechastock;
						console.log(option);
						if (element.cerrado == false)
						{
                            this.op_stocks.push(option);
						}
					});
				},
				async sumarProductos() {
					this.btn_producto = true;
					this.numero = this.numero + 1;
					// this.resetFormAdh();
					// this.cantidad=this.cantidad +1;
					this.ventas.componentes.push({
						cantidad: 0,
						producto: null,
						precio: 0,
					});
					this.validacion.componentes.push({
						cantidad: { estado: null, mensaje: "" },
						producto: { estado: null, mensaje: "" },
						precio: { estado: null, mensaje: "" },
					});
				},
				async eliminarProducto(index) {
                    if (this.ventas.componentes.length == 1) {
                        alert("Tiene que haber al menos 1 producto");
					} else
					{
                        this.ventas.componentes.splice(index, 1)
                        this.validacion.componentes.splice(index, 1)
                        this.totalprod.pop(this.numero);
                        this.numero = this.numero - 1;
					}
				},
				
				cargarFeedback(respuesta) {
					let valido;
					if (!respuesta) respuesta = {};
					for (let key in this.validacion) {
						valido = !respuesta.hasOwnProperty(key);
						this.validacion[key].estado = valido;
						if (!valido) this.validacion[key].mensaje = respuesta[key][0];
					}
					this.cargarFeedbackcomponentes(respuesta)
				},
				async handleInput() {
                    let productosAPI = new APIControler();
                    productosAPI.apiUrl.pathname = "stocks/";
                    this.listastocks = await productosAPI.getData();
                    if (this.$session.get('tipocuenta') == 'reparto')
					{
						this.ventas.id_reparto = this.$session.get('stockused').id_reparto
						this.ventas.id_stock = "http://93.188.162.174:8081/stocks/" + this.$session.get('stockused').id_stock + "/"
						this.getClientesRep();
						this.repartohabilitado = true;
                        this.stockhabilitado = true;
                        this.setStock();
					}
				},
				handleInput2()
				{
                    var today = new Date();
                    today.setDate(today.getDate());
                    var date = today.getFullYear() + "-" + ('0' + (today.getMonth() + 1)).slice(-2) + "-" + ('0' + today.getDate()).slice(-2);
                    console.log(String(date));
					this.ventas.fecharealizacion = date;
					this.ventas.tipo_venta = 'Pollo';
					this.ventas.totalpagado = 0;
                    this.ventas.totalpagadomp = 0;
				},
				cargarFeedbackcomponentes(respuesta) {
					let valido;
					let ok=false
					if (!respuesta.componentes) ok=true;
					this.validacion.componentes.forEach((b,index )=> {
						for (let key in b) {
							valido = ok || !respuesta.componentes[index].hasOwnProperty(key);
							b[key].estado = valido;
							console.log(key);
							if (!valido) b[key].mensaje = respuesta.componentes[index][key][0];
						}
					});

				},
			},
			beforeMount() {
                this.getStocks()
				this.getProductos()
				this.getRepartos()
				this.getClientes()
				this.sumarProductos()
				this.getCategorias()
				this.handleInput()
                this.handleInput2()
			},
			/*
	  computed: {
		  validation() {
			return this.text.length > 0 ? true : false;
		  },
		}
	  */
		};
	</script>

